	module.exports = {"data":{"postsJson":{"title":"Council at Jerusalem","questionsTitle":"DIGGING DEEPER","display":"block","bookName":"사도행전","ref":"15:1-15:36","html":"<p>   <strong>15</strong><u>1</u>어떤 사람들이 유대로부터 내려와서 형제들을 가르치되</p><h1>유대교 전파자</h1><h4>너희가 모세의 법대로 할례를 받지 아니하면 능히 구원을 받지 못하리라</h4><p>하니 <u>2</u>바울 및 바나바와 그들 사이에 적지 아니한 다툼과 변론이 일어난지라 형제들이 이 문제에 대하여 바울과 바나바와 및 그 중의 몇 사람을 예루살렘에 있는 사도와 장로들에게 보내기로 작정하니라 <u>3</u>그들이 교회의 전송을 받고 베니게와 사마리아로 다니며 이방인들이 주께 돌아온 일을 말하여 형제들을 다 크게 기쁘게 하더라<br />   <u>4</u>예루살렘에 이르러 교회와 사도와 장로들에게 영접을 받고 하나님이 자기들과 함께 계셔 행하신 모든 일을 말하매 <u>5</u>바리새파 중에 어떤 믿는 사람들이 일어나 말하되</p><h1>바리새파중 믿는 사람들</h1><h4>이방인에게 할례를 행하고 모세의 율법을 지키라 명하는 것이 마땅하다</h4><p>하니라<br />   <u>6</u>사도와 장로들이 이 일을 의논하러 모여 <u>7</u>많은 변론이 있은 후에 베드로가 일어나 말하되</p><h1>시몬 베드로</h1><h3>형제들아 너희도 알거니와 하나님이 이방인들로 내 입에서 복음의 말씀을 들어 믿게 하시려고 오래 전부터 너희 가운데서 나를 택하시고 <u>8</u>또 마음을 아시는 하나님이 우리에게와 같이 그들에게도 성령을 주어 증언하시고<u>9</u>믿음으로 그들의 마음을 깨끗이 하사 그들이나 우리나 차별하지 아니하셨느니라 <u>10</u>그런데 지금 너희가 어찌하여 하나님을 시험하여 우리 조상과 우리도 능히 메지 못하던 멍에를 제자들의 목에 두려느냐 <u>11</u>그러나 우리는 그들이 우리와 동일하게 주 예수의 은혜로 구원 받는 줄을 믿노라</h3><p>하니라<br />   <u>12</u>온 무리가 가만히 있어 바나바와 바울이 하나님께서 자기들로 말미암아 이방인 중에서 행하신 표적과 기사에 관하여 말하는 것을 듣더니 <u>13</u>말을 마치매 야고보가 대답하여 이르되</p><h1>예수님의 형제 야고보</h1><h4>형제들아 내 말을 들으라 <u>14</u>하나님이 처음으로 이방인 중에서 자기 이름을 위할 백성을 취하시려고 그들을 돌보신 것을 시므온이 말하였으니 <u>15</u>선지자들의 말씀이 이와 일치하도다 기록된 바<br />   <u>16</u>이후에 내가 돌아와서<br />   다윗의 무너진 장막을 다시 지으며<br />   또 그 허물어진 것을 다시 지어 일으키리니<br />   <u>17</u>이는 그 남은 사람들과<br />   내 이름으로 일컬음을 받는<br />   모든 이방인들로 주를 찾게 하려 함이라<br />   하셨으니 <u>18</u>즉 예로부터 이것을 알게 하시는 주의 말씀이라 함과 같으니라<br />   <u>19</u>그러므로 내 의견에는 이방인 중에서 하나님께로 돌아오는 자들을 괴롭게 하지 말고 <u>20</u>다만 우상의 더러운 것과 음행과 목매어 죽인 것과 피를 멀리하라고 편지하는 것이 옳으니 <u>21</u>이는 예로부터 각 성에서 모세를 전하는 자가 있어 안식일마다 회당에서 그 글을 읽음이라</h4><p>하더라<br />   <u>22</u>이에 사도와 장로와 온 교회가 그 중에서 사람들을 택하여 바울과 바나바와 함께 안디옥으로 보내기를 결정하니 곧 형제 중에 인도자인 바사바라 하는 유다와 실라더라 <u>23</u>그 편에 편지를 부쳐 이르되</p><h1>예루살렘 공회</h1><h4>사도와 장로 된 형제들은 안디옥과 수리아와 길리기아에 있는 이방인 형제들에게 문안하노라<br />   <u>24</u>들은즉 우리 가운데서 어떤 사람들이 우리의 지시도 없이 나가서 말로 너희를 괴롭게 하고 마음을 혼란하게 한다 하기로 <u>25-26</u>사람을 택하여 우리 주 예수 그리스도의 이름을 위하여 생명을 아끼지 아니하는 자인 우리가 사랑하는 바나바와 바울과 함께 너희에게 보내기를 만장일치로 결정하였노라<u>27</u>그리하여 유다와 실라를 보내니 그들도 이 일을 말로 전하리라<br />   <u>28</u>성령과 우리는 이 요긴한 것들 외에는 아무 짐도 너희에게 지우지 아니하는 것이 옳은 줄 알았노니 <u>29</u>우상의 제물과 피와 목매어 죽인 것과 음행을 멀리할지니라 이에 스스로 삼가면 잘되리라 평안함을 원하노라</h4><p>하였더라<br />   <u>30</u>그들이 작별하고 안디옥에 내려가 무리를 모은 후에 편지를 전하니 <u>31</u>읽고 그 위로한 말을 기뻐하더라<br />   <u>32</u>유다와 실라도 선지자라 여러 말로 형제를 권면하여 굳게 하고 <u>33</u>얼마 있다가 평안히 가라는 전송을 형제들에게 받고 자기를 보내던 사람들에게로 돌아가되 <u>34</u>(없음) <u>35</u>바울과 바나바는 안디옥에서 유하며 수다한 다른 사람들과 함께 주의 말씀을 가르치며 전파하니라<br />   <u>36</u>며칠 후에 바울이 바나바더러 말하되</p><h1>바울</h1><h3>우리가 주의 말씀을 전한 각 성으로 다시 가서 형제들이 어떠한가 방문하자</h3><p>하고</p>","path":"/358","Question1":"What issue did the apostles and elders gather in Jerusalem to consider?","Question2":"According to Peter in this story how are believers saved?","Question3":"Do you feel the need to fulfill certain religious expectations to be saved?","Question4":"Just as the Holy Spirit confirmed the truth of the gospel to apostles and elders, what is the Holy Spirit saying to you?","prevPath":"/357","nextPath":"/359"}},"pathContext":{}}

